.header{
    margin-top: 10%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
}

h4 {
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}

ul {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
ul.dashed {
  list-style-type: none;
}
ul.dashed > li {
  text-indent: -10px;
}
ul.dashed > li:before {
  content: "-";
  text-indent: -10px;
}